@font-face {
  font-family: 'Chalkduster';
  src: url(/static/media/chalkduster.bd9805cb.ttf) format('truetype'), url(/static/media/chalkduster.db0913bc.woff) format('woff');
}  

body {
  margin: 0;
  padding: 0;
  font-family: nunito, sans-serif, Chalkduster;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  background-image: -webkit-linear-gradient(#f3acba, #3a4164);
  background-image: linear-gradient(#f3acba, #3a4164);
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: inherit;
  text-decoration: inherit;
}

* { -webkit-overflow-scrolling: touch; }
.App {
  color: #3a4164;
  width: 100%;
  height: 100%;
}
.CardProfile {
  max-width: 100%;
  width: 1040px;
  height: auto;
  margin: 40px auto 0 auto;
  border-radius: 40px;
  background-color: white;
  position: relative;
}
.hl-top {
  background-image: url(data:image/png;base64,);
  background-repeat: space;
  background-size: 60px auto;
  max-width: 100%;
  height: 20px;
  margin: 0 40px;
}
.hl-bottom {
  background-image: url(data:image/png;base64,);
  background-repeat: space;
  background-size: 60px auto;
  position: absolute;
  min-width: 93%;
  height: 20px;
  margin: 0 40px;
  bottom: 0;
  left: 0;
}
.PersonalInfo, .Headline { position: relative; }
.PersonalInfo { width: 37%; float: right; margin: 10px 4% 0 2%; }
.Badge { width: 100%; margin-top: 10px; }
.PersonalInfo .Info h1 {
  font-family: courier-prime, monospace;
  font-weight: 700;
  font-style: normal;
  letter-spacing: -2px;
  color: #f98ea1;
  font-size: 38px;
  margin: 5px 0; 
}
.ContactInfo { list-style: none; padding: 0; margin-top: 0; margin-bottom: 45px; }
.ContactInfo li {
  font-weight: 500;
  font-style: normal;
  padding: 0 30px 10px 15px;
  display: -webkit-inline-flex;
  display: inline-flex;
  line-height: 22px;
  width: 100%;
}
.ContactInfo li::before { 
  content: '';
  background-repeat: no-repeat;
  width: 20px;
  margin-right: 20px;
  margin-top: 3.5px;
}
.Home::before { background-image: url(data:image/png;base64,); background-size: 20px auto; }
.Email::before { background-image: url(data:image/png;base64,); background-size: 20px auto; }
.Tel::before { background-image: url(data:image/png;base64,); background-size: auto 18px; }
.Headline { width: 50%; float: left; margin: 40px 3% 0 4%; }
.Quote { position: absolute; width: 100px; height: auto; }
.Headline h2 {
  font-family: chalkduster;
  font-weight: normal;
  font-size: 26px;
  line-height: 44px;
  position: relative;
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
  padding: 0 0 0 20px;
  z-index: 1000; }
.vl {
  position: absolute;
  border-left: 1.5px dashed #3a4164;
  height: 340px;
  margin: 25px 0;
  left: 55%;
}

@media only screen and (max-width: 1023px) {
  .PersonalInfo .Info h1 { font-size: 32px; }
  .Headline { margin-top: 20px; }
}

@media only screen and (max-width: 850px) {
  .CardProfile { width: auto; margin: 10px; }
  .PersonalInfo, .Headline { 
    margin: 40px auto;
    width: 89%;
    float: unset;
  }
  .PersonalInfo { padding-bottom: 10px; }
  .PersonalInfo .Info h1 { font-size: 32px; }
  .ContactInfo li { padding: 0 0 10px 0; }
  .ContactInfo li::before { width: 23px; }
  .Quote { width: 75px; }
  .Headline h2 { font-size: 18px; line-height: 36px; }
  .vl { 
    left: unset; 
    -webkit-transform: rotate(90deg); 
            transform: rotate(90deg); 
    position: relative;
    margin-top: 60px;
    margin-bottom: -380px;
  }
  .hl-bottom { min-width: 80%; }
}
.CardProfile, .CardDes, .CardEdu { -webkit-animation: fadeInDown .8s ease-in both; animation: fadeInDown .8s ease-in both; }
.CardDes { -webkit-animation-delay: .8s; animation-delay: .8s; }
.CardEdu { -webkit-animation-delay: 1.6s; animation-delay: 1.6s; }
.CardExp, .CardPort { -webkit-animation: fadeIn .8s ease-in both; animation: fadeIn .8s ease-in both; }
.CardExp { -webkit-animation-delay: 2.4s; animation-delay: 2.4s; }
.CardPort { -webkit-animation-delay: 3.2s; animation-delay: 3.2s; }

@-webkit-keyframes fadeInDown {
	from {
		opacity: 0;
		-webkit-transform: translate3d(0, -20%, 0);
		        transform: translate3d(0, -20%, 0);
	}
	to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}

@keyframes fadeInDown {
	from {
		opacity: 0;
		-webkit-transform: translate3d(0, -20%, 0);
		        transform: translate3d(0, -20%, 0);
	}
	to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@-webkit-keyframes fadeIn {
	from {
		opacity: 0;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
	to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@keyframes fadeIn {
	from {
		opacity: 0;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
	to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}

.CardDes {
  max-width: 100%;
  width: 1040px;
  height: 570px;
  margin: 20px auto;
  top: -300px;
  left: -30px;
  border-radius: 40px;
  background-color: #3a4164;
  position: relative;
  z-index: -1;
}
.DesInfo {
  position: absolute;
  bottom: 0;
}
.ProfileDes {
  color: white;
  display: inline-block;
  padding: 20px 60px;
  width: 90%;
}
.hl {
  width: 100%;
  border-top: 2px dashed #f98ea1;
}
.Skills {
  display: table;
  margin: 0 auto;
}
.Skills h2 {
  font-family: chalkduster;
  font-weight: normal;
  font-size: 32px;
  color: #f98ea1;
  position: relative;
  display: inline-block;
  margin-right: 30px;
  top: -10px;
}
.SkillIcon {
  position: relative;
  display: inline-block;
  margin: 30px 0;
}
.SketchIcon, .XDIcon, .PSIcon, .AIIcon, .InvisIcon, .CodeIcon {
  height: auto;
  margin: 0 5px;
  position: relative;
  display: inline-block;
}
.SketchIcon { width: 45px; }
.XDIcon, .PSIcon, .AIIcon, .InvisIcon, .CodeIcon { width: 41px; }

@media only screen and (max-width: 850px) {
  .CardDes { width: auto; left: 0; margin: 5px; height: 800px; }
  .ProfileDes { width: auto; padding: 30px; }
  .Skills { width: 100%; }
  .Skills h2 {
    text-align: center;
    width: 100%;
    display: table;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    margin-bottom: 15px;
  }
  .SkillIcon { width: 100%; margin: 0 auto 30px auto; display: table; text-align: center; }
  .SketchIcon, .XDIcon, .PSIcon, .AIIcon, .InvisIcon, .CodeIcon { width: 38px; }
}
.CardExp {
    max-width: 100%;
    width: 1040px;
    height: auto;
    margin: 30px auto;
    border-radius: 40px;
    background-color: white;
    position: relative;
    top: -300px;
    z-index: -3;
}
.ExpInfo { padding: 20px 60px 5px 60px; }
.ExpInfo h3 {
    color: #f98ea1;
    font-weight: 800;
    font-size: 28px;
    margin-top: 10px;
    margin-bottom: 30px;
    display: inline-block;
}
.Exp { margin-bottom: 30px; }
.ExpInfo .Info { margin-top: 20px; }
.Year { width: 18%; float: left; color: #9097be; }
.Dot { width: 20px; float: left; margin-top: 5px; }
.ExpInfo .Info .Details { width: 75%; float: right; }
.Position, .Company { margin-left: 15px; }
.Year, .Position { font-weight: 800; font-size: 20px; }
.Company, .Location { color: #9097be; display: inline-block; }
.Company::after {
    content: '|';
    color: #e1e2eb;
    padding: 5px;
}
.ExpDes {
    background-color: #f9f5f6;
    border-radius: 20px;
    margin: 15px 0;
    padding: 5px 30px;
}
.clear { clear:both; }
.arrow-down {
    width: 0; 
    height: 0; 
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid #f98ea1;
    position: relative;
    display: inline-block;
    top: -5px;
    margin-left: 30px;
}
.arrow-right {
    width: 0; 
    height: 0; 
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: 10px solid #ffb0be;
}

@media only screen and (max-width: 850px) {
    .CardExp { width: auto; margin: 10px; }
    .ExpInfo { padding: 10px 15px; }
    .ExpInfo h3 { margin-left: 10px; }
    .Year, .Dot { position: absolute; }
    .Year { width: 50%; padding-left: 40px; }
    .Dot { padding-left: 10px; }
    .ExpInfo .Info .Details { width: 100%; padding-top: 40px; }
    .Position, .Company { margin-left: 10px; }
    .Company, .Location { font-size: 13px; }
}
.CardEdu {
    width: 400px;
    height: 110px;
    position: absolute;
    left: 53%;
    top: 750px;
    background-image: url(data:image/png;base64,);
    z-index: -2;
}
.EduIcon {
    width: 45px;
    height: auto;
    margin: 25px 0 20px 35px;
    float: left;
}
.EduInfo {
    margin: 20px 35px 0 0;
    float: right;
}
.EduInfo .Degree { font-weight: 800; }

@media only screen and (max-width: 850px) {
    .CardEdu {
        position: relative;
        width: auto;
        margin: 0 38px;
        left: 0;
        top: -306px;
    }
    .EduIcon { width: 20px; margin: 18px 0 20px 10px; }
    .EduInfo { margin: 15px 0 0 10px; float: unset; }
    .EduIcon, .EduInfo { display: inline-block; position: relative; }
}
.CardPort {
    max-width: 100%;
    width: 1040px;
    height: auto;
    margin: 0 auto;
    position: relative;
    top: -300px;
}
.PortHeader h1 {
    font-family: courier-prime, monospace;
    font-size: 120px;
    font-weight: 800;
    text-align: center;
    margin-bottom: 0;
 }
 .PortHeader h2 {
     color: white;
    font-family: courier-prime, monospace;
    font-size: 50px;
    font-weight: 800;
    text-align: center;
    margin: -20px 0 0 0;
 }
.PortInfo { margin-top: 120px; }
.PortInfo .Feature {
    width: 50%;
    height: 400px;
    position: relative;
    display: -webkit-inline-flex;
    display: inline-flex;
    float: left;
    border-radius: 20px;
}
.MakeABag { background-image: url(/static/media/makeabag.6b4ac80b.jpg); background-size: 100% auto; background-repeat: no-repeat; }
.CFConfig { background-image: url(/static/media/cfconfig.10748799.jpg); background-size: 100% auto; background-repeat: no-repeat; }
.CFStore { background-image: url(/static/media/cfstore.05bf4089.jpg); background-size: 100% auto; background-repeat: no-repeat; }
.PortInfo .Details { width: 50%; float: right; }
.PortTitle { margin: 10px 0 0 60px; }
.PortTitle h1 {
    color: white;
    font-weight: 800;
    font-size: 50px;
    line-height: 50px;
    margin: 0;
}
.PortDes { margin: 40px 0 0 60px; color: white; }
.PortLink ul { margin: 10px 0 0 60px; padding: 10px 0; list-style-type: none; }
.PortLink .mPrototype, .PortLink .tPrototype, .CompanySite {
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
}
.PortLink .mPrototype, .PortLink .tPrototype {
    width: 30%;
    color: #f98ea1;
    text-align: center;
    margin: 8px 8px 0 0;
    padding: 8px 30px;
    display: inline-block;
    border-radius: 5px;
    background-color: #3a4164;
    -webkit-transition: background-color 0.5s ease;
    transition: background-color 0.5s ease;
}
.PortLink .mPrototype:hover, .PortLink .tPrototype:hover { background-color: #4c577f; }
.PortLink .CompanySite {
    color: #FFF6CE;
    padding-top: 20px;
    text-decoration: underline;
    -webkit-transition: color 0.5s ease;
    transition: color 0.5s ease;
}
.PortLink .CompanySite:hover { color: white; }

@media only screen and (max-width: 850px) {
    .PortHeader h1 { font-size: 80px; }
    .PortHeader h2 { font-size: 42px; }
    .PortInfo { margin-top: 80px; }
    .PortInfo .Feature { border-radius: 0; height: 300px; }
    .PortInfo .Feature, .PortInfo .Details { width: 100%; float: unset;}
    .PortInfo .Details { padding-top: 40px; }
    .PortTitle { text-align: center; margin: 0; }
    .PortDes { margin: 30px 40px; }
    .PortLink ul { margin: 0 40px; padding: 0; }
    .PortLink .mPrototype, .PortLink .tPrototype { width: auto; margin: 10px 0; padding: 10px auto; display: block; }
    .PortLink .CompanySite { text-align: center; }
}
